.newageux__blog {
    display: flex;
    flex-direction: column;
}

.newageux__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.newageux__blog-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
    }

    .newageux__blog-container {
        display: flex;
        flex-direction: row;
    }
    
    .newageux__blog-container_groupA {
       flex: 0.75;
       margin-right: 2rem;
    }
    
    .newageux__blog-container_groupB {
       flex: 1;
       margin-right: 2rem;
       display: grid;
       grid-template-columns: repeat(2, 1fr);
       grid-gap: 2rem;
    }

    @media screen and (max-width: 990px) {
        .newageux__blog-container {
            flex-direction: column-reverse;
        }

        .newageux__blog-container_groupA {
            margin: 2rem 0;
        }

        .newageux__blog-container_groupA .newageux__blog-container_article {
            width: 48%;
        }
        
        .newageux__blog-container_groupA .newageux__blog-container_article-image {
          height: 250px;
        }
    }

    @media screen and (max-width: 700px) {
        .newageux__blog-container_groupB {
            grid-template-columns: repeat(1, 1fr);
        }

        .newageux__blog-container_groupA .newageux__blog-container_article {
            width: 100%
        }
        
        .newageux__blog-heading h1 {
            font-size: 46px;
            line-height: 52px;
        }
    }

    @media screen and (max-width: 550px) {
        .newageux__blog-heading h1 {
            font-size: 34px;
            line-height: 42px;
        }
    }